































































































.uppy-progress {
  position: relative;
}

.uppy-upload {
  display: block;
  margin: auto;
  padding: 5px 15px;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 0;
  border: 1px solid gray;
  background: none;
  cursor: pointer;
  margin-top: 15px;
}

.uppy-upload:hover {
  background: gray;
}

// .uppy-DragDrop-container:focus {
//   box-shadow: none
// }

.uppy-upload-icon {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  margin-top: 80px;
  z-index: 99;

  svg {
    width: 40px;
  }
}

.img {
  max-width: 100px;
}
